<template>
  <form @submit.prevent="handleSubmit">
      <h3>Giriş Yap</h3>
      <input type="email" placeholder="Email" v-model="email">
      <input type="password" placeholder="Şifre" v-model="password">
      <div v-if="error" class="error"> {{ error }}</div>
      <button v-if="!isPending">Giriş Yap</button>
      <button v-if="isPending" :class="{ 'succesfully' : isPending }"  disabled>Bekleyiniz giriş yapılıyor...</button>
  </form>
</template>

<script>

import useLogin from '@/composables/useLogin'
import { ref } from "vue"
import { useRouter } from "vue-router"

export default {
    setup(){
        const {error, login, isPending} = useLogin()

        const router = useRouter()

        const email = ref('')
        const password = ref('')

        const handleSubmit = async () => {
            const res = await login(email.value, password.value)
            if(!error.value)  {
                console.log('User logged in!')
                router.push({ name : 'UserPlaylists' })
            }
        }

        return {
            email,
            password,
            handleSubmit,
            error,
            isPending
        }
    }
}
</script>

<style scoped>

.succesfully{
    background-color: rgb(20, 133, 20);
    color: black;
}


</style>